import React,{Component} from 'react'
import {Icon, NavBar, WingBlank, WhiteSpace, Card, Button,Toast,List,TextareaItem} from "antd-mobile";
import axios from "axios";
import {Redirect} from "react-router-dom";

class ActivityDetail extends Component{

    constructor(props){
        super(props);
        this.state = {
            allowRender:true,
            role:'',
            lastPage:'',
            activityId:'0',
            topic:'',
            location:'',
            start:'',
            end:'',
            totalCount:'',
            description:'',
            userList:[],
            noticeVOList:[],
            newNotice:'',
            shareUrl:''
        };

        this.handleLeftClick = this.handleLeftClick.bind(this);
        this.handleShare = this.handleShare.bind(this);
        this.handleAddNotice = this.handleAddNotice.bind(this);
    }

    componentWillMount(){
        // 分割，并取最后一个
        let array = window.location.pathname.split('/');
        let activityId = array.pop();
        let lastPage = array.pop();
        this.setState({
            activityId,
            lastPage
        })
    }

    componentDidMount(){

        // 先渲染数据
        axios.defaults.withCredentials=true;
        let url = 'http://118.31.34.71:9997/activity/'+this.state.activityId+'/info';
        axios.get(url).then(data=>{
            data = data.data.data;
            console.log(data);
            this.setState({
                role:data.userVO.role,
                topic:data.activityVO.topic,
                location:data.activityVO.location,
                description:data.activityVO.description,
                start:data.activityVO.start,
                end:data.activityVO.end,
                totalCount:data.activityVO.totalCount,
                userList:data.userVOList,
                noticeVOList:data.noticeVOList
                // noticeVOList:['公告1公告1公告1公告1公告1公告1公告1公告1公告1公告1公告1公告1',
                //     '公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2公告2']
            })
        }).catch((err)=>{
            console.log('ActivityDeatail界面获取数据出错');
            this.setState({
                allowRender : false
            })
        })
    }

    handleLeftClick(){
        if(this.state.lastPage === 'created'){
            this.props.history.push('/activityCreatedByMyself')
        }else if (this.state.lastPage === 'takePartIn') {
            this.props.history.push('/activityTakePartIn')
        }else{
            this.props.history.push('/main')
        }
    }

    handleShare(){
        let url = 'http://localhost:3000/share/'+this.state.activityId;
        Toast.success('分享链接已在下方生成，赶紧向小伙伴们分享吧~',2);
        console.log(url);
        this.setState({
            shareUrl:url
        })


    }

    handleAddNotice(){
        console.log(this.state.newNotice);
        axios.defaults.withCredentials = true;
        let url = 'http://118.31.34.71:9997/notice/save';
        let that = this;
        axios.post(url,{
            activityId:this.state.activityId,
            info:this.state.newNotice
        }).then(()=>{
            let array = JSON.parse(JSON.stringify(that.state.noticeVOList));
            array.unshift({info:that.state.newNotice});
            that.setState({
                noticeVOList: array,
                newNotice:''
            })

        })
    }

    render() {



        if(!this.state.allowRender){
            return <Redirect to={'/'}/>
        }

        const item = {
          border:'1px solid #eee',
            borderRadius:'10px',
            marginTop:'10px',
            fontWeight:'bolder',
            padding:'5px'
        };

        return (
            <div>
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    onLeftClick={this.handleLeftClick}
                >{this.state.topic}</NavBar>

                <WingBlank size="lg">
                    <WhiteSpace size="lg" />
                    <WhiteSpace size="lg" />
                    <Card>
                        <Card.Header title={this.state.topic} style={{fontWeight:'bolder'}}/>
                        <Card.Body>
                            <div style={item}>
                                <span>活动时间：</span>
                                <ul>
                                    <li>开始：{this.state.start}</li>
                                    <li>结束：{this.state.end}</li>
                                </ul>
                            </div>
                            <div style={item}>
                                <span>活动描述：</span>
                                <ul>
                                    <li>{this.state.description}</li>
                                </ul>
                            </div>
                            <div style={item}>
                                <span>活动地点：</span>
                                <ul>
                                    <li>{this.state.location}</li>
                                </ul>
                            </div>
                            <div style={item}>
                                <span>人数限制：</span>
                                <span>{this.state.totalCount}人</span>
                            </div>
                            <div style={item}>
                                <span>活动公告：</span>
                                <ul>{this.state.noticeVOList.map((item)=>{return (<li>{item.info}</li>)})}</ul>
                                <div style={{display:this.state.role === 'OWNER'?'block':'none'}}>
                                    <List>
                                        <TextareaItem
                                            rows={5}
                                            count={100}
                                            value={this.state.newNotice}
                                            onChange={(newNotice)=>{this.setState({newNotice})}}
                                        />
                                    </List>
                                    <Button size={'small'} type={'ghost'} onClick={this.handleAddNotice}>新增公告</Button>
                                </div>
                            </div>
                            <div style={item}>
                                <span>成员：</span>
                                <ul>
                                    {this.state.userList.map(item=>{
                                        return <li>{item.user}</li>
                                    })}
                                </ul>
                            </div>
                        </Card.Body>
                        <Button style={{display: this.state.role === 'OWNER'?'block':'none' ,margin:'10px auto'}}
                                type="primary" inline size="small"
                                onClick={this.handleShare}
                                className="am-button-borderfix">分享</Button>
                        <List style={{display:this.state.shareUrl!==''?'block':'none'}}>
                            <TextareaItem
                                rows={1}
                                value={this.state.shareUrl}
                            />
                        </List>
                    </Card>
                    <WhiteSpace size="lg" />
                </WingBlank>
            </div>
        );
    }
}

export default ActivityDetail;