import React , {Component} from 'react'
import { Card, WingBlank, WhiteSpace ,Button , List , TextareaItem,Toast} from 'antd-mobile';
import axios from "axios";

class ShareActivityShowCard extends Component{

    constructor(props){
        super(props);
        this.handleAddClick = this.handleAddClick.bind(this);
        this.handleIgnoreClick = this.handleIgnoreClick.bind(this);
        this.handleSubmitClick = this.handleSubmitClick.bind(this)
        this.state = {
            add:false,
            reason:''
        }
    }

    handleAddClick(){
        console.log('jiaru')
        this.setState({
            add:true
        })
    }

    // 因为只有分享页面只有单个item，忽略了就直接跳回首页
    handleIgnoreClick(){
        this.props.history.push('/main')
    }

    handleSubmitClick(){

        axios.defaults.withCredentials=true;
        axios.post('http://118.31.34.71:9997/activity/join',{
            activityId:this.props.id,
            reason:this.state.reason
        }).then(data=>{
            Toast.success('提交成功,等待活动发起者审核',4);
            this.props.history.push('/main');
        }).catch((err)=>{
            console.log('申请出错');
        })
    }

    render() {
        const boxShadow = {
          boxShadow : '0 5px 10px 5px #eee'
        };

        return (
            <div>

                <WingBlank size="lg">
                    <WhiteSpace size="lg" />
                    <WhiteSpace size="lg" />
                    <Card style={boxShadow}>
                        <Card.Header title={this.props.topic} style={{fontWeight:'bolder'}}>
                        </Card.Header>
                        <Card.Body>
                            <div>
                                <span>活动时间：</span>
                                <ul>
                                    <li>开始：{this.props.start}</li>
                                    <li>结束：{this.props.end}</li>
                                </ul>
                            </div>
                            <div style={{marginTop:'5px',marginBottom:'5px'}}>
                                <span>活动地点：</span>
                                <ul>
                                    <li>{this.props.location}</li>
                                </ul>
                            </div>
                            <div style={{textAlign:'center',marginTop:'10px',display: this.state.add === false?'block':'none' }}>
                                <Button style={{marginRight:'50px'}} type="primary" inline size="small" onClick={this.handleAddClick}>⭕加入</Button>
                                <Button type="ghost" inline size="small" className="am-button-borderfix" onClick={this.handleIgnoreClick}>❌忽略</Button>
                            </div>
                            <div style={{display: this.state.add === true?'block':'none',textAlign:'center'}}>
                                <List renderHeader={() => '申请理由'}>
                                    <TextareaItem
                                        rows={5}
                                        count={100}
                                        value={this.state.reason}
                                        onChange={(reason)=>{this.setState({reason})}}
                                    />
                                </List>
                                <Button style={{marginTop:'10px'}} type="primary" inline size="small" onClick={this.handleSubmitClick}>提交</Button>
                            </div>
                        </Card.Body>
                    </Card>
                    <WhiteSpace size="lg" />
                </WingBlank>
            </div>
        );
    }

}

export default ShareActivityShowCard;