import React,{Component} from 'react';
import {Card, TabBar, WhiteSpace, WingBlank} from "antd-mobile";
import ShareActivityShowCard from "../ShareActivityShowCard";
import ActivityShowCard from "../ActivityShowCard";
import axios from 'axios';

class FirstTabContent extends Component{


    constructor(props){
        super(props);
        this.state = {
            isShare:false,
            //首页数据
            myActivity:[],
            shareActivity:''
        }
    }

    componentWillMount(){
        // console.log(this.props.history);
    }

    componentDidMount(){
        // 获取数据
        if(window.location.href.match('/share/')){

            this.setState({
               isShare:true
            });

            let array = window.location.pathname.split('/');
            let activityId = array.pop();
            console.log(activityId);

            axios.defaults.withCredentials=true;
            axios.get('http://118.31.34.71:9997/share/'+activityId).then((data)=>{
                console.log(data.data.data);
                this.setState({
                    shareActivity: data.data.data
                })
            });


            console.log('这是分享链接')
        }else{

            console.log('这不是是分享链接')
            axios.defaults.withCredentials=true;
            axios.get('http://118.31.34.71:9997/activity/list').then(data=>{
                // console.log(data);
                let myActivity = data.data.data;
                // console.log(myActivity);
                this.setState({
                    myActivity
                })
            }).catch((err)=>{
                //不错任何处理
                console.log('没有登录，不能获取第一页内容');
            })
        }
    }

    render(){

        return (<div>
            {/*这里要条件渲染，没有活动，分享活动进入，我的活动，这些都不一样*/}
            {/*如果活动为0，显示默认情况*/}

            <div style={{display: this.state.isShare === true ?'block':'none' }}>
                <ShareActivityShowCard id={this.state.shareActivity.id}
                                       topic={this.state.shareActivity.topic}
                                       location={this.state.shareActivity.location}
                                       start={this.state.shareActivity.start}
                                       end={this.state.shareActivity.end}
                                       history={this.props.history}

                />
            </div>


            <div style={{display:this.state.isShare === false && this.state.myActivity.length === 0 ?'block':'none' }}>
                {/*<div>没有活动</div>*/}

                <WingBlank size="lg">
                    <WhiteSpace size="lg" />
                    <WhiteSpace size="lg" />
                    <Card>
                        <Card.Header title="😊你还没参加任何活动哦~" style={{fontWeight:'bolder'}}/>
                        <Card.Body>
                            <div>
                                <span>快点击创建活动或通过好友分享参加吧@_@</span>
                            </div>
                        </Card.Body>
                    </Card>
                    <WhiteSpace size="lg" />
                </WingBlank>

            </div>
            <div style={{display:this.state.isShare === false && this.state.myActivity.length !== 0 ?'block':'none' }}>

                {/*<ActivityShowCard status='OWNER' topic='冰雪大世界' location='哈尔滨' start='2019年01月14日' end='2019年02月14日'/>*/}
                {/*<ActivityShowCard status='MEMBER' topic='冰雪大世界' location='哈尔滨' start='2019年01月14日' end='2019年02月14日'/>*/}
                {/*<ActivityShowCard status='PREMEMBER' topic='冰雪大世界' location='哈尔滨' start='2019年01月14日' end='2019年02月14日'/>*/}
                {/*<ActivityShowCard status='EXITMEMBER' topic='冰雪大世界' location='哈尔滨' start='2019年01月14日' end='2019年02月14日'/>*/}
                {/*<ActivityShowCard status='UNMEMBER' topic='冰雪大世界' location='哈尔滨' start='2019年01月14日' end='2019年02月14日'/>*/}

                {this.state.myActivity.map( (item) =>{
                    return <ActivityShowCard key={item.activityId}
                                             history={ this.props.history }
                                             isWhichPage = 'main'
                                             id={item.activityId}
                                             status={item.status}
                                             topic={item.activityVO.topic}
                                             location={item.activityVO.location}
                                             start={item.activityVO.start}
                                             end={item.activityVO.end} />
                })}

            </div>
                </div>)
    }

}

export default FirstTabContent;