import React, { Component ,Fragment} from 'react';
import Index from './pages/Index'
import Main from './pages/Main'
import Map from './pages/Map'
import ActivityCreatedByMyself from './pages/ActivityCreatedByMyself'
import ActivityTakePartIn from './pages/ActivityTakePartIn'
import ActivityDetail from './pages/ActivityDetail'
import {BrowserRouter,Route} from 'react-router-dom';
import 'antd-mobile/dist/antd-mobile.css';


class App extends Component {

  render() {

    return (
      <div className="max-height">
          <BrowserRouter>
            <Fragment>
                <Route path = '/' exact component={Index}></Route>
                <Route path = '/main' exact component={Main}></Route>
                <Route path = '/main/:tab' exact component={Main}></Route>
                <Route path = '/activity/detail/:page/:id' exact component={ActivityDetail}></Route>
                <Route path ='/map' exact component={Map}></Route>
                {/*分享链接也进入main*/}
                <Route path = '/share/*' exact component={Main}></Route>
                <Route path = '/activityCreatedByMyself' exact component={ActivityCreatedByMyself}></Route>
                <Route path = '/activityTakePartIn' exact component={ActivityTakePartIn}></Route>
            </Fragment>
          </BrowserRouter>
      </div>
    );
  }
}

export default App;
