import React,{Component} from 'react'
import { WingBlank, WhiteSpace  ,List, InputItem,DatePicker,Stepper,Button ,Toast} from 'antd-mobile';
import axios from 'axios';

const nowTimeStamp = Date.now();
const now = new Date(nowTimeStamp);

class AddActivity extends Component{

    constructor(props){
        super(props);
        this.state = {
            startDate: now,
            endDate:now,
            time: now,
            val:1,
            topic:'',
            description:'',
            location:''
        };

        this.onButtonClick = this.onButtonClick.bind(this);
    }

    onButtonClick(){
        var data = {
            topic: this.state.topic,
            description: this.state.description,
            location: this.state.location,
            start: this.state.startDate.getFullYear()+'年'+(parseInt(this.state.startDate.getMonth())+1)+'月'
                +this.state.startDate.getDate()+'日'+this.state.startDate.getHours()+'时'+this.state.startDate.getMinutes()+'分',
            end: this.state.endDate.getFullYear()+'年'+(parseInt(this.state.endDate.getMonth())+1)+'月'
                +this.state.endDate.getDate()+'日'+this.state.endDate.getHours()+'时'+this.state.endDate.getMinutes()+'分',
            totalCount:this.state.val
        };

        axios.defaults.withCredentials=true;

        axios.get('http://118.31.34.71:9997/info').then((data)=>{
            console.log(data)
        }).catch((err)=>{
            console.log(err);
        })

        // console.log(data);
        axios.post('http://118.31.34.71:9997/activity/save',data).then((data)=>{
            console.log(data);
            Toast.success('发布成功',3);
            // 这样重定向能够强制刷新首页
            setTimeout(()=>{
                window.location.pathname = '/main'
            },3000);

        }).catch((err)=>{
            console.log(err);
            Toast.fail('发布失败',2)
        })
    }

    componentDidMount(){
        this.setState({
            topic:window.sessionStorage.getItem('activityTopic'),
            description:window.sessionStorage.getItem('activityDesc'),
            location:window.sessionStorage.getItem('activityAddress')
        })
    }

    render() {

        const container = {
          // border : '2px solid black',
            width:'86%',
            margin:'0 auto',
            marginTop :'40px',
            borderRadius : '20px',
            // backgroundColor : 'rgba(17, 125, 147, 0.7)'
            backgroundColor: '#1296db'

        };

        const animation = {
            animation : 'add-activity 0.5s 1 linear'
        };

        const triangle = {
            height:'40px',
            width:'60px',
            backgroundColor:'transparent',
            margin: '0 auto',
            borderTop: '50px solid #1296db',
            borderLeft: '30px solid transparent',
            borderRight: '30px solid transparent',
            borderBottom: '50px solid transparent',

        };

        return (
            <div style={animation}>
                <div style={container}>
                <WingBlank>
                    <WhiteSpace size='xl'/>
                    <WhiteSpace/>
                    <List>
                        {/*ref={el => this.autoFocusInst = el}*/}
                        <InputItem value={this.state.topic} onChange={(topic)=>{this.setState({topic})}}>活动主题</InputItem>
                    </List>
                    <WhiteSpace/>
                    <List>
                        <InputItem value={this.state.description} onChange={(description)=>{this.setState({description})}}>活动描述</InputItem>
                    </List>
                    <WhiteSpace/>
                    <List>
                        <InputItem placeholder={'使用高德地图定位'} value={this.state.location} onChange={(location)=>{this.setState({location})}} onFocus={()=>{

                            window.sessionStorage.setItem('activityTopic',this.state.topic);
                            window.sessionStorage.setItem('activityDesc',this.state.description);
                            this.props.history.push('/map')
                        }}>活动位置</InputItem>
                    </List>
                    <WhiteSpace/>
                    <List>
                        <DatePicker
                            mode='datetime'
                            title="Select Date"
                            extra="Optional"
                            value={this.state.startDate}
                            onChange={startDate => this.setState({ startDate })}
                        >
                            <List.Item arrow="horizontal">开始时间</List.Item>
                        </DatePicker>
                    </List>
                    <WhiteSpace/>
                    <List>
                        <DatePicker
                            mode='datetime'
                            title="Select Date"
                            extra="Optional"
                            value={this.state.endDate}
                            onChange={endDate => this.setState({ endDate})}
                        >
                            <List.Item arrow="horizontal">结束时间</List.Item>
                        </DatePicker>
                    </List>
                    <WhiteSpace/>
                    <List>
                        <List.Item
                            wrap
                            extra={
                                <Stepper
                                    style={{ width: '100%' }}
                                    showNumber
                                    max={10}
                                    min={1}
                                    value={this.state.val}
                                    onChange={val => this.setState({ val})}
                                />}
                        >
                            活动人数
                        </List.Item>
                    </List>
                    <WhiteSpace/>
                    <WhiteSpace size='xl'/>
                    <Button onClick={this.onButtonClick}>发布</Button>
                    <WhiteSpace/>
                    <WhiteSpace size='xl'/>
                </WingBlank>
                </div>
                <div style={triangle}></div>
            </div>
        );
    }

}

export default AddActivity;