import React , {Component} from 'react'
import { Card, WingBlank, WhiteSpace ,Button} from 'antd-mobile';
import axios from "axios";

class ActivityShowCard extends Component{

    constructor(props){
        super(props);
        this.state = {
            showCheck:false,
            userList:[]
        };

        this.handleShowDetail = this.handleShowDetail.bind(this);
        this.handlePersonCheck = this.handlePersonCheck.bind(this);
        this.handleShowDetail2 = this.handleShowDetail2.bind(this);
    }

    handleShowDetail(){
        //获取活动详情
        // console.log(this.props.history);
        this.props.history.push('/activity/detail/'+this.props.isWhichPage+'/'+this.props.id);
    }

    handleShowDetail2(){
        //获取活动详情
        // console.log(this.props.history);
        this.props.history.push('/activity/detail/takePartIn/'+this.props.id);
    }

    handlePersonCheck(){

        this.setState({
            showCheck:true
        });

        axios.defaults.withCredentials=true;
        axios.get('http://118.31.34.71:9997/activity/list/'+this.props.id+'/users').then((data)=>{
           console.log(data);
           this.setState({
               userList:data.data.data
           })
        });
    }

    handleAgress(userId){
        // 点击同意或拒绝之后，重新更新
        axios.defaults.withCredentials=true;
        axios.post('http://118.31.34.71:9997/activity/agree/'+this.props.id+'/'+userId).then((data)=>{
            this.handlePersonCheck()
        });
    }

    handleReject(userId){
        // 点击同意或拒绝之后，重新更新
        axios.defaults.withCredentials=true;
        axios.post('http://118.31.34.71:9997/activity/reject/'+this.props.id+'/'+userId).then((data)=>{
            this.handlePersonCheck()
        });
    }


    render() {

        const boxShadow = {
            boxShadow : '0 5px 10px 5px #eee'
        };

        const item = {
            border:'1px solid #eee',
            padding:'10px',
            borderRadius : '20px'
        };

        return (
            <div>
                <WingBlank size="lg">
                    <WhiteSpace size="lg" />
                    <WhiteSpace size="lg" />
                    <Card style={boxShadow}>
                        <Card.Header title={this.props.topic} style={{fontWeight:'bolder'}}>
                        </Card.Header>
                        <Card.Body>
                            <div>
                                <span>活动时间：</span>
                                <span>{this.props.start} ~ {this.props.end}</span>
                            </div>
                            <div style={{marginTop:'5px',marginBottom:'5px'}}>
                                <span>活动地点：</span>
                                <span>{this.props.location}</span>
                            </div>
                            <div style={{display: this.props.isWhichPage === 'main'?'block':'none',textAlign:'center',marginTop:'10px'}}>
                                {/*判断状态，通过display来显示*/}
                                <Button style={{display:this.props.status === 'MEMBER'?'block':'none'}} type="primary" inline size="small" className="am-button-borderfix" onClick={this.handleShowDetail} ><span role='img'>✅</span>审核通过</Button>
                                <Button style={{display:this.props.status === 'OWNER'?'block':'none'}} type="warning" inline size="small" className="am-button-borderfix" onClick={this.handleShowDetail}><span role='img'>🙋</span>我发布的</Button>
                                <Button style={{display:this.props.status === 'PREMEMBER'?'block':'none'}} type="ghost" inline size="small" className="am-button-borderfix"><span role='img'>🚫</span>审核中</Button>
                                <Button style={{display:this.props.status === 'UNMEMBER'?'block':'none'}} type="ghost" inline size="small" className="am-button-borderfix"><span role='img'>🚫</span>被拒绝</Button>
                                <Button style={{display:this.props.status === 'EXITMEMBER'?'block':'none'}} type="ghost" inline size="small" className="am-button-borderfix"><span role='img'>🚫</span>已退出</Button>
                            </div>
                            <div style={{display: this.props.isWhichPage === 'takePartIn'?'block':'none',textAlign:'center',marginTop:'10px'}}>
                                {/*判断状态，通过display来显示*/}
                                <Button style={{display:this.props.status === 'MEMBER'?'block':'none'}} type="primary" inline size="small" className="am-button-borderfix" onClick={this.handleShowDetail2} ><span role='img'>✅</span>审核通过</Button>
                                <Button style={{display:this.props.status === 'PREMEMBER'?'block':'none'}} type="ghost" inline size="small" className="am-button-borderfix"><span role='img'>🚫</span>审核中</Button>
                            </div>
                            <div style={{display: this.props.isWhichPage === 'created'?'block':'none',textAlign:'center',marginTop:'10px'}}>

                                {/*按钮部分*/}
                                <div style={{display:this.state.showCheck === false?'block':'none'}}>
                                    <Button style={{marginRight:'20px'}} type="primary"
                                            inline size="small"
                                            onClick={this.handlePersonCheck}
                                            className="am-button-borderfix">人员审核</Button>
                                    <Button type="ghost" inline size="small" className="am-button-borderfix" onClick={this.handleShowDetail}>活动详情</Button>
                                </div>

                                {/*人员审核部分*/}
                                <div style={{display:this.state.showCheck === true?'block':'none'}}>
                                    <div style={{fontWeight:'bolder'}}>申请名单</div>

                                    {this.state.userList.map((person,index)=>{
                                        return (<div style={item}>
                                            <div>
                                                <span>用户名 : </span>
                                                <span>{person.userVO.user}</span>
                                            </div>
                                            <div>
                                                <span>申请理由 : </span>
                                                <span>{person.reason}</span>
                                            </div>
                                            <div>
                                                <Button style={{marginRight:'20px'}} type="primary"
                                                        inline size="small"
                                                        onClick={this.handleAgress.bind(this,person.userId,index)}
                                                        className="am-button-borderfix">同意</Button>
                                                <Button type="warning"
                                                        inline size="small"
                                                        onClick={this.handleReject.bind(this,person.userId,index)}
                                                        className="am-button-borderfix">拒绝</Button>
                                            </div>

                                        </div>)
                                    })}

                                    <Button type="ghost"
                                            inline size="small"
                                            onClick={()=>{this.setState({showCheck:false})}}
                                            className="am-button-borderfix">返回</Button>
                                </div>

                            </div>
                        </Card.Body>
                    </Card>
                    <WhiteSpace size="lg" />
                </WingBlank>
            </div>
        );
    }

}

export default ActivityShowCard;