import React,{Component} from 'react'
import {NavBar, Icon, WingBlank, WhiteSpace, Card} from 'antd-mobile';
import ActivityShowCard from "../../components/ActivityShowCard";
import axios from "axios";
import {Redirect} from "react-router-dom";

class ActivityCreatedByMyself extends Component{

    constructor(props){
        super(props);
        this.state = {
            allowRender:true,
            myActivity:[]
        }
    }

    componentDidMount(){
        // console.log(this.props.history);
        axios.defaults.withCredentials=true;
        axios.get('http://118.31.34.71:9997/activity/list/member').then(data=>{
            let myActivity = data.data.data;
            this.setState({
                myActivity
            })
        }).catch((err)=>{
            console.log('created界面获取数据出错');
            this.setState({
                allowRender : false
            })
        });
    }

    render() {

        if(!this.state.allowRender){
            return <Redirect to={'/'}/>
        }

        return (
            <div>
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {this.props.history.push('/main/3')}}
                >我参加的活动</NavBar>

                <div style={{display: this.state.myActivity.length === 0 ?'block':'none' }}>
                    <WingBlank size="lg">
                        <WhiteSpace size="lg" />
                        <WhiteSpace size="lg" />
                        <Card>
                            <Card.Header title="😊你还没参加任何活动哦~" style={{fontWeight:'bolder'}}/>
                            <Card.Body>
                                <div style={{fontWeight:'bolder'}}>
                                    <span>没有活动</span>
                                </div>
                            </Card.Body>
                        </Card>
                        <WhiteSpace size="lg" />
                    </WingBlank>
                </div>

                <div style={{display:this.state.myActivity.length !== 0 ?'block':'none' }}>

                    {this.state.myActivity.map( (item) =>{
                        return <ActivityShowCard key={item.activityId}
                                                 history = {this.props.history}
                                                 isWhichPage = {'takePartIn'}
                                                 id={item.activityId}
                                                 status={item.status}
                                                 topic={item.activityVO.topic}
                                                 location={item.activityVO.location}
                                                 start={item.activityVO.start}
                                                 end={item.activityVO.end} />
                    })}

                </div>

            </div>
        );
    }
}

export default ActivityCreatedByMyself;