import React,{Component} from 'react'
import {Redirect} from 'react-router-dom';
import {Tabs, Toast,WhiteSpace,WingBlank, List, InputItem, Button} from 'antd-mobile';
import axios from 'axios';
import backImage from '../../static/images/back.jpg'

class Index extends Component{

    constructor(props){
        super(props);
        this.state = {
            allowRender:true,
            loginUser:'',
            loginPassword:'',
            registerEmail:'',
            registerUser:'',
            registerPassword:'',
            confirmPassword:''
        };

        this.handleLogin = this.handleLogin.bind(this);
        this.handleRegister = this.handleRegister.bind(this);
        this.handleLoginUserChange = this.handleLoginUserChange.bind(this);
        this.handleLoginPasswordChange = this.handleLoginPasswordChange.bind(this);
        this.handleRegisterEmail = this.handleRegisterEmail.bind(this);
        this.handleRegisterUser = this.handleRegisterUser.bind(this);
        this.handleRegisterPassword = this.handleRegisterPassword.bind(this);
        this.handleConfirmPassword = this.handleConfirmPassword.bind(this);


    }

    componentDidMount(){
        axios.defaults.withCredentials=true;
        axios.get('http://118.31.34.71:9997/info').then((data)=>{
            console.log(data.data);
            this.setState({
                allowRender:false
            })
        }).catch(err=>{
            //报错其实就是要登录
        })
    }

    handleLogin(){
        let email = this.state.loginUser;
        let pwd = this.state.loginPassword;

        // 只支持单域名且为com的邮箱
        let reg1 = /[a-zA-Z0-9]+@[a-zA-z]+\.[a-z]+$/;
        let reg2 = /[a-zA-Z0-9]+/;

        if((email === '' || email == null || !reg1.test(email))){
            Toast.fail('用户名格式错误', 2);
        }else if(pwd === '' || pwd == null || !reg2.test(pwd)){
            Toast.fail('密码格式错误', 2);
        }else{
            // 请求凭证
            axios.defaults.withCredentials=true;
            axios.post('http://118.31.34.71:9997/login',{
                email,
                pwd
            }).then((data)=>{
                console.log(data);
                data = data.data;
                if(data.code === 200 && data.msg === '请求成功'){
                    Toast.success('登录成功', 2);

                    this.setState({
                        allowRender:false
                    });

                    this.props.history.push('/main')

                }else {
                    Toast.fail('登录失败', 2);
                }
            }).catch((err)=>{
                console.log('登录失败');
                console.log(err);
                //400 当作异常处理了
                Toast.fail(err.response, 2);
                Toast.fail(err.message, 2);
            });
        }


    }

    handleRegister(){

        // 只支持单域名且为com的邮箱
        let reg1 = /[a-zA-Z0-9]+@[a-zA-z]+\.[a-z]+/;
        let reg2 = /[a-zA-Z0-9\u4e00-\u9fa5]+/;
        let reg3 = /[a-zA-Z0-9]+/;

        let email = this.state.registerEmail;
        let user = this.state.registerUser;
        let pwd = this.state.registerPassword;
        let cpwd = this.state.confirmPassword;

        if(email === '' || email == null || !reg1.test(email)){
            Toast.fail('邮箱不符合规范', 2);
        }else if(user === '' || user == null || !reg2.test(user)){
            Toast.fail('用户名不符合规范', 2);
        }else if(pwd === '' || pwd == null || !reg3.test(pwd)){
            Toast.fail('密码不符合规范', 2);
        }else if(pwd !== cpwd){
            Toast.fail('两次密码输入不正确', 2);
        }else{
            axios.post('http://118.31.34.71:9997/register', {
                email,
                user,
                pwd
            }).then((data)=>{
                data = data.data;
                if(data.code === 200 && data.msg === '请求成功'){
                    Toast.success('注册成功', 2);
                }else {
                    Toast.fail('注册失败', 2);
                }
            }).catch((err)=>{
                console.log(err);
                Toast.fail('注册失败', 2);
                // Toast.fail('服务器故障', 2);
            });

        }


    }

    handleLoginUserChange(event) {
        // event竟然就是值！
        this.setState({
            loginUser:event
        })
    }

    handleLoginPasswordChange(value){
        this.setState({
            loginPassword:value
        })
    }

    handleRegisterEmail(value){
        this.setState({
            registerEmail:value
        })
    };

    handleRegisterUser(value){
        this.setState({
            registerUser:value
        })
    };

    handleRegisterPassword(value){
        this.setState({
            registerPassword:value
        })
    }

    handleConfirmPassword(value){
        this.setState({
            confirmPassword:value
        })
    }

    render(){

        // 登录状态下,不应该跳转到登录界面
        if(!this.state.allowRender){
            console.log('/界面已经登录');
            return <Redirect to='/main'/>
        }

        const backGround = {
            backgroundImage:`url(${backImage})`,
            backgroundSize:'cover',
            backgroundRepeat:'no-repeat'
        };

        const tabs = [
            { title: 'LOG IN', sub: '1' },
            { title: 'SIGN UP', sub: '2' }
        ];

        return (
            <div style={backGround} className='max-height'>
                <Tabs tabs={tabs}
                      initialPage={0}
                      tabBarPosition="top"
                      renderTab={tab => <span>{tab.title}</span>}
                      // 设置rgba能保证背景透明，子元素不透明
                ><div style={{ height: '100%', backgroundColor: '#f5f5f9', background:'rgba(255,255,255,0.15)'}}>
                    <WhiteSpace size='xl'/>
                    <WhiteSpace size='xl'/>
                    <WhiteSpace size='xl'/>
                    {/*两翼留白*/}
                    <WingBlank>
                        <List>
                            <InputItem value={this.state.loginUser} onChange={this.handleLoginUserChange} placeholder="请输入账号">账号</InputItem>
                        </List>
                        <WhiteSpace size='xl'/>
                        <List>
                            <InputItem value={this.state.loginPassword} onChange={this.handleLoginPasswordChange} type="password" placeholder="请输入密码">密码</InputItem>
                        </List>
                    </WingBlank>

                    <WhiteSpace size='xl'/>
                    <WhiteSpace size='xl'/>
                    <WhiteSpace size='xl'/>
                    <WhiteSpace size='xl'/>
                    <WingBlank>
                        <Button type="primary" onClick={this.handleLogin}>登录</Button><WhiteSpace/>
                    </WingBlank>
                </div>
                    <div style={{ height: '100%', backgroundColor: '#f5f5f9', background:'rgba(255,255,255,0.15)'}}>
                        <WhiteSpace size='xl'/>
                        <WhiteSpace size='xl'/>
                        <WhiteSpace size='xl'/>
                        {/*两翼留白*/}
                        <WingBlank>
                            <List>
                                <InputItem placeholder="请输入邮箱"
                                           value={this.state.registerEmail}
                                           onChange={this.handleRegisterEmail} >邮箱</InputItem>
                                <InputItem placeholder="请输入用户名"
                                           value={this.state.registerUser}
                                           onChange={this.handleRegisterUser}
                                >用户名</InputItem>
                                <InputItem type="password"
                                           placeholder="请输入密码"
                                           value={this.state.registerPassword}
                                           onChange={this.handleRegisterPassword}
                                >密码</InputItem>
                                <InputItem type="password"
                                           placeholder="请再次输入密码"
                                           value={this.state.confirmPassword}
                                           onChange={this.handleConfirmPassword}
                                >确认密码</InputItem>
                            </List>
                        </WingBlank>

                        <WhiteSpace size='xl'/>
                        <WhiteSpace size='xl'/>
                        <WhiteSpace size='xl'/>
                        <WhiteSpace size='xl'/>
                        <WingBlank>
                            <Button type="primary" onClick={this.handleRegister}>注册</Button>
                        </WingBlank>

                    </div>
                </Tabs>
            </div>
        )
    }

}

export default Index;
