import React,{Component} from 'react'
import {TabBar, NavBar,WingBlank,Button,WhiteSpace,Card,Pagination} from 'antd-mobile';
import {Redirect} from 'react-router-dom';
import userIcon from '../../static/images/user.png';
import userActiveIcon from '../../static/images/userActive.png'
import addIcon from '../../static/images/add.png';
import addActiveIcon from '../../static/images/addActive.png'
import homeIcon from '../../static/images/home.png';
import homeActiveIcon from '../../static/images/homeActive.png'
import AddActivity from '../../components/AddActivity';
import FirstTabContent from '../../components/FirstTabContent';
import icon from '../../static/images/icon.png'
import bulb from '../../static/images/bulb.png'
import team from '../../static/images/team.png'
import axios from "axios";



class Main extends Component{

    constructor(props){
        super(props);
        this.state = {
            allowRender:true,
            selectedTab:'首页',
            showAddActivity:'none',
            user:''
        };
        this.handleLogout = this.handleLogout.bind(this);
    }

    // 在这阶段会合并state
    componentWillMount(){
        if(window.location.pathname.match('/main/2')){
            this.setState({
                selectedTab:'发布活动'
            })
        }else if (window.location.pathname.match('/main/3')){
            this.setState({
                selectedTab:'我的'
            })
        }else {
            this.setState({
                selectedTab:'首页'
            })
        }
    }

    componentDidMount(){
        axios.defaults.withCredentials=true;
        axios.get('http://118.31.34.71:9997/info').then((data)=>{
            // console.log(data.data);
            this.setState({
                user:data.data.data.user
            })
        }).catch(err=>{
            this.setState({
                allowRender:false
            })
        })
    }

    // 登出并修改redux
    handleLogout(){

        axios.defaults.withCredentials=true;
        let result = axios.get('http://118.31.34.71:9997/logout');
        result.then(()=>{
            // 直接刷新页面
            window.location.pathname = '/'
        }).catch((err)=>{
            console.log('登出出错');
        })

    }

    render() {

        if(!this.state.allowRender){
            console.log('Main界面没有登录');
            return <Redirect to={'/'}/>
        }

        return (
            <div className='max-height'>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white">
                    <TabBar.Item title="首页"
                                 key="首页"
                                 icon={<div style={{
                                     width: '20px',
                                     height: '20px',
                                     background: `url(${homeIcon}) center center /  cover no-repeat`}}
                                 />}
                                 selectedIcon={<div style={{
                                     width: '25px',
                                     height: '25px',
                                     background: `url(${homeActiveIcon}) center center /  cover no-repeat`}}
                                 />}
                                 onPress={() => {
                                     this.setState({
                                         selectedTab: '首页'
                                     });
                                 }}
                                 selected={this.state.selectedTab === '首页'}
                    >
                        <NavBar mode="dark">Young Travel</NavBar>
                        <div style={{display:this.state.selectedTab === '首页'?'block':'none'}}>
                            <FirstTabContent history={this.props.history} />
                        </div>
                    </TabBar.Item>
                    <TabBar.Item
                                 key="SignUp"
                                 icon={<div style={{
                                     width: '40px',
                                     height: '40px',
                                     background: `url(${addIcon}) center center /  cover no-repeat`}}
                                 />}
                                 selectedIcon={<div style={{
                                     width: '45px',
                                     height: '45px',
                                     background: `url(${addActiveIcon}) center center /  cover no-repeat`}}
                                 />}
                                 onPress={() => {
                                     this.setState({
                                         selectedTab: '发布活动'
                                     });
                                 }}
                                 selected={this.state.selectedTab === '发布活动'}
                    >
                        <NavBar mode="dark">Young Travel</NavBar>
                        <div style={{'display': this.state.selectedTab === '发布活动'?'block':'none'}}>
                            <AddActivity history={this.props.history} />
                        </div>
                    </TabBar.Item>
                    <TabBar.Item title="我的"
                                 key="我的"
                                 icon={<div style={{
                                     width: '20px',
                                     height: '20px',
                                     background: `url(${userIcon}) center center /  21px 21px no-repeat`}}
                                 />}
                                 selectedIcon={<div style={{
                                     width: '25px',
                                     height: '25px',
                                     background: `url(${userActiveIcon}) center center /  cover no-repeat`}}
                                 />}
                                 onPress={() => {
                                     this.setState({
                                         selectedTab: '我的'
                                     });
                                 }}
                                 selected={this.state.selectedTab === '我的'}
                    >
                        <NavBar mode="light">Young Travel</NavBar>
                        <Button type='primary' style={{height:'150px'}} icon={<img alt='头像' style={{height:'100px',width:'100px',borderRadius:'50%'}} src={icon}/>}>{this.state.user}</Button>
                        <WingBlank>
                        <div style={{marginTop:'20px'}}>
                            <Button icon={<img alt='我参加的活动' src={team}/>} onClick={() => {this.props.history.push('/activityTakePartIn')}} >我参加的活动</Button>
                            <WhiteSpace/>
                            <Button icon={<img alt='我创建的活动' src={bulb}/>} onClick={() => {this.props.history.push('/activityCreatedByMyself')}} >我创建的活动</Button>
                        </div>
                        </WingBlank>

                        <div style={{position:'absolute',bottom:0,width:'100%'}}>
                                <Button type={'warning'} onClick={this.handleLogout}>退出登录</Button>
                        </div>


                    </TabBar.Item>

                </TabBar>
            </div>
        );
    }
}

export default Main;