import React, {Component} from 'react'
import {Map, Marker} from 'react-amap'
import {NavBar, List, Button, TextareaItem} from 'antd-mobile'

class MyMap extends Component {
    constructor() {
        super();
        const _this = this;
        this.map = null;
        this.geocoder = null;
        this.marker = null;
        this.mapEvents = {
            created(map) {
                _this.map = map;
                const AMap = window.AMap
                AMap.plugin('AMap.Geocoder', () => {
                    _this.geocoder = new AMap.Geocoder({
                        city: "010"//城市，默认：“全国”
                    });
                });
            },
            click(e) {
                const lnglat = e.lnglat;
                _this.setState({
                    position: lnglat,
                    currentLocation: 'loading...'
                });
                _this.geocoder && _this.geocoder.getAddress(lnglat, (status, result) => {
                    // console.log(result);
                    if (status === 'complete') {
                        if (result.regeocode) {
                            _this.setState({
                                currentLocation: result.regeocode.formattedAddress || '未知地点'
                            });
                        } else {
                            _this.setState({
                                currentLocation: '未知地点'
                            });
                        }
                    } else {
                        _this.setState({
                            currentLocation: '未知地点'
                        });
                    }
                })
            }
        };
        this.state = {
            position: {longitude: 118.780086, latitude: 32.049808},
            currentLocation: '点击地图选择活动地点哦😄'
        };
    }

    transferAddressToAdd(){
        window.sessionStorage.setItem('activityAddress',this.state.currentLocation);
        this.props.history.push('/main/2')
    }

    render() {
        const plugins = [
            'Scale',
            {
                name: 'ToolBar',
                options: {
                    visible: true,
                    onCreated(ins) {
                    },
                },
            }
        ];

        const listStyle = {
            position: 'absolute',
            top: '10px',
            width:'98%'
        }

        return (
            <div style={{width: '100%', height: '100%'}}>
                <NavBar>Young Travel</NavBar>
                <div className="map-container" style={{width: '100%', height: '95%'}}>
                    <Map plugins={plugins} zoom={15} center={this.state.position} events={this.mapEvents}>
                        <Marker position={this.state.position} draggable="true"/>
                        <List.Item style={listStyle}  extra={<Button type="primary" size="small" inline onClick={this.transferAddressToAdd.bind(this)}>确定</Button>}>
                            <TextareaItem style={{fontSize: '15px'}} value={this.state.currentLocation} rows={2}/>
                        </List.Item>
                    </Map>
                </div>

            </div>
        )

    }
}

export default MyMap;